import { Component, OnInit } from '@angular/core';
import { NavController, ActionSheetController, LoadingController } from '@ionic/angular';
import {  AppMin } from "../core"


interface objlist {
  userid: string,
  couponName,//优惠券名称
  couponText,//优惠券说明
  couponMoney,//优惠金额
  date,//有效期

}



@Component({
  selector: 'app-my-coupon',
  templateUrl: './my-coupon.page.html',
  styleUrls: ['./my-coupon.page.scss'],
})
export class MyCouponPage implements OnInit {
  objlist: objlist[];

  header_class;
  loader;
  notMoer: boolean = true;
  constructor(public appmin: AppMin, public navCtrl: NavController, public actionSheetCtrl: ActionSheetController, public loadingCtrl?: LoadingController) {

    if (appmin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      console.log("判断为安卓系统 设置状态栏样式")
    }




  }
  ionViewWillEnter() {
    //每次即将加载页面

  }
  ionViewDidEnter() {
    //每次已经进入页面
   
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad MywalletPage');
  }
  send(value, isshow?: boolean) {


    if (isshow) {
      this.loader = this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000

      });
      this.loader.present();
    }


    this.appmin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })
    

 

  }

  addMyCouponPage() {
    let obj = {
      type: "addMyCouponPage",
      userId: this.appmin.getUserid(),
    }

    this.send(JSON.stringify(obj), false);
  }
  dataHandle(meg: any) {
    console.log(meg)
    if (meg['type'] == "addMyCouponPage_ret") {
      this.objlist = meg['rows'];
      if (this.objlist.length != 0) {
        this.notMoer = false;
      }

    }
  }

  ngOnInit() {
    this.addMyCouponPage();
  }

}
